//
import Vue from 'vue';
import VueRouter from 'vue-router';
//
import Login from '@/components/pages/login';
import Dashboard from '@/components/dashboard';
import Products from '@/components/pages/products';
import Orders from '@/components/pages/orders';
import Coupon from '@/components/pages/coupon';
import ShoppingDemo from '@/components/pages/shoppingdemo';
import OrderCheckout from '@/components/pages/orderCheckout';
import Index from '@/components/index';
import About from '@/components/pages/about';
import Gamehost from '@/components/gameHost';
import GamehostMenu from '@/components/pages/gameHostMenu';
import Switch from '@/components/pages/switch';
import Ps4 from '@/components/pages/ps4';
import N3ds from '@/components/pages/n3ds';

Vue.use(VueRouter);

export default new VueRouter({
    //
    scrollBehavior (to, from, savedPosition) {
        if (savedPosition) {
          return savedPosition
        } else {
          return { x: 0, y: 0 }
        }
    },
    routes: [
        {
            path: '*',
            redirect: '/index'
        },
        {
            path: '/',
            redirect: '/index',
            component: Index,
            children: [
                {
                    name:"",
                    path: 'index',
                    component: About,
                },
                {
                    path: 'gamehost',
                    component: Gamehost,
                    children: [
                        {
                            name:"",
                            path: '/',
                            component: GamehostMenu,
                        },
                        {
                            name:"-Switch",
                            path: 'switch',
                            component: Switch,
                        },
                        {
                            name:"-PS4",
                            path: 'ps4',
                            component: Ps4,
                        },
                        {
                            name:"-N3DS",
                            path: 'n3ds',
                            component: N3ds,
                        },
                    ],
                },
                {
                    name:"CrossGate",
                    path: 'shopping',
                    component: About,
                },
            ],
        },
        //
        {
            path: '/login',
            component: Login,
        },
        //
        {
            path: '/admin',
            redirect: '/login',
            // redirect  path redirect
            component: Dashboard, 
            children: [
                //  "/"    "/"
                {
                    path: 'products',
                    component: Products,
                    meta: { requiresAuth: true },
                },
                {
                    path: 'orders',
                    component: Orders,
                    meta: { requiresAuth: true },
                },
                {
                    path: 'coupon',
                    component: Coupon,
                    meta: { requiresAuth: true },
                },
                {
                    path: 'shopping-demo',
                    component: ShoppingDemo,
                    meta: { requiresAuth: true },
                },
                {
                    path: 'orderCheckout/:orderID',
                    component: OrderCheckout,
                    meta: { requiresAuth: true },
                },
            ],
        },
    ],
});


